body {
  padding: 15px 0;
  font-family: 'Itim', sans-serif;
}

.App {
  text-align: center;
}

h1 {
  font-family: 'Rubik Marker Hatch', sans-serif;
  font-size: 72px;
  color: #0ccac4;
  margin-block-start: 0.3em;
  margin-block-end: 0.3em;
}

@media screen and (max-width: 768px) {
  h1 {
    font-size: 56px;
  }
}